<template>
    <div class='pinglun'>
        <div class="logo">
            <div class="left">
                <img src="../assets/img/shangcheng/logo.png" alt="">
            </div>
            <div class="right">
            </div>
        </div>
        <div class="title">
            {{$t('pingjia[0]')}}
        </div>
        <div class="con">
            <div class="text">
                <textarea type="textarea" :placeholder="$t('pingjia[1]')+'...'" v-model="textarea" />
                <div class="upload">
                    <el-upload
                        action="http://192.168.1.113/v1/upload_file"
                        list-type="picture-card"
                        name="image"
                        :on-success="handleAvatarSuccess"
                        :on-preview="handlePictureCardPreview"
                        :on-remove="handleRemove">
                        <i class="el-icon-plus"></i>
                    </el-upload>
                    <el-dialog :visible.sync="dialogVisible">
                        <img width="100%" :src="dialogImageUrl" alt="">
                    </el-dialog>
                </div>
                <div class="xingji">
                    <div>{{$t('pingjia[2]')}}</div>
                    <div><el-rate v-model="value1"></el-rate></div>
                </div>
            </div>
            <div class="fabu">
                <div @click="fabu()">{{$t('pingjia[3]')}}</div>
            </div>
            <div class="lishi" id="pingjia">
                <div class="title1">
                    {{$t('pingjia[4]')}}
                </div>
                <div class="list">
                    <div class="no" v-if="show">{{$t('listTitle[8]')}}</div>
                    <div class="item" v-for="(item,i) in commit_list" :key="i">
                        <div class="left">{{item.name}}</div>
                        <div class="right">
                            <div><el-rate v-model="item.star" disabled></el-rate></div>
                            <div>{{item.content}}</div>
                            <div>
                                <div v-for="(img,j) in item.image" :key="j">
                                    <img :src="imgUrl + img" alt="" @click="imgClick(imgUrl + img)">
                                </div>
                            </div>
                            <div>{{item.time}}</div>
                        </div>
                    </div>
                </div>
                <div style="text-align: right;">
                    <div style="display: inline-block;">
                        <el-pagination
                            @current-change="handleCurrentChange"
                            :page-size="pageSize"
                            layout="prev, pager, next, jumper"
                            :total="pageTptal">
                        </el-pagination>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    data() {
        return {
            dialogImageUrl: '',
            dialogVisible: false,
            disabled: false,
            value1:0,//星级
            textarea:'',
            commit_list:'',//评论列表
            pageSize:8,//每页显示条数
            pageTptal:1,//总条数
            total_page:1,//总页数
            show:'',
            imageAll:'',
        };
    },
    created:function(){
    },
    mounted:function(){
        var _this = this;
        _this.imgUrl = _this.apis.second;
        _this.apis.get(_this.apis.commit_list,{params:{lang:_this.apis.lang,page:1,pageNumber:_this.pageSize}}).then(function(res) {
            if(res.data.code == 0 ){
                if(res.data.data.total<1){
                    _this.pageTptal = res.data.data.total
                    _this.total_page = res.data.data.per_page
                    _this.commit_list = res.data.data.data
                    _this.show = true
                }else{
                    _this.show = false;
                    _this.pageTptal = res.data.data.total
                    _this.total_page = res.data.data.last_page
                    _this.commit_list = res.data.data.data
                }
            }
        })
    },
    watch:{
    },
    methods:{
        //获取评价列表
        pingjiaList(val){
            var _this = this;
            _this.apis.get(_this.apis.commit_list,{params:{lang:_this.apis.lang,page:val,pageNumber:_this.pageSize}}).then(function(res) {
                if(res.data.code == 0 ){
                    if(res.data.data.total<1){
                        _this.pageTptal = res.data.data.total
                        _this.total_page = res.data.data.per_page
                        _this.commit_list = res.data.data.data
                        _this.show = true
                    }else{
                        _this.show = false;
                        _this.pageTptal = res.data.data.total
                        _this.total_page = res.data.data.last_page
                        _this.commit_list = res.data.data.data
                    }
                }
            })
        },
        //发布评价
        fabu(){
            var _this = this;
            if(!_this.value1 || !_this.textarea || !_this.imageAll){
                _this.$message({
                    message: _this.$t('pingjia[5]'),
                    type: 'info'
                });
                return;
            }
            _this.apis.post(_this.apis.commit,{lang:_this.apis.lang,star:_this.value1,content:_this.textarea,image:_this.imageAll.slice(0,_this.imageAll.length-1)}).then(function(res) {
                if(res.data.code == 0){
                    _this.$message({
                        message: _this.$t('pingjia[6]'),
                        type: 'success'
                    });
                    _this.value1 = 0
                    _this.textarea = ''
                    _this.imageAll = ''
                    $('.el-upload-list').html('')
                    _this.pingjiaList();
                }
            })
        },
        shangchuan(){
            var _this = this;
            console.log(1)
        },
        //分页
        handleCurrentChange(val) {
            var _this = this;
            console.log(val)
            _this.getList(val);
            window.location.hash = "#pingjia";
        },
        //获取评论列表
        getList(p,shiian,county,keyword){
            var _this = this;
            _this.apis.get(_this.apis.commit_list,{params:{page:p,pageNumber:_this.pageSize,lang:_this.apis.lang}}).then(function(res) {
                if(res.data.code == 0){
                    if(res.data.data.total<1){
                        _this.pageTptal = res.data.data.total
                        _this.total_page = res.data.data.per_page
                        _this.commit_list = res.data.data.data
                        _this.show = true
                    }else{
                        _this.show = false;
                        _this.pageTptal = res.data.data.total
                        _this.total_page = res.data.data.last_page
                        _this.commit_list = res.data.data.data
                    }
                }
            })  
        },


        handleAvatarSuccess(res, file) {
            var _this = this;
            this.imageUrl = URL.createObjectURL(file.raw);
            _this.imageAll += res.data+','
        },
        handleRemove(file, fileList) {
            var _this = this;
            console.log(file, fileList);
            for(let i=0;i<fileList.length;i++){
                _this.imageAll += fileList[i].response.data + ','
            }
        },
        handlePictureCardPreview(file) {
            this.dialogImageUrl = file.url;
            this.dialogVisible = true;
        },
        imgClick(file) {
            this.dialogImageUrl = file;
            this.dialogVisible = true;
        }
    },
    components:{
    }
}
</script>
<style lang='less' scoped>
.pinglun{
    margin-bottom: 50px;
}
.logo{
    width: 1360px;
    height: 137px;
    margin: 0 auto;
    padding-top: 20px;
    display: flex;
    justify-content: space-between;
    align-items: top;
    .left{
        width: 444px;
        height: 87px;
    }
    .right{
        width: 639px;
        .sousuo2{
            width: 640px;
            height: 43px;
            line-height: 43px;
            margin: 0 auto;
            margin-bottom: 10px;
            display: flex;
            background-image: linear-gradient(to right, #FFC344 , #FFAD00);
            &>div:nth-child(1){
                width: 541px;
                height: 39px;
                margin-left: 2px;
            }
            &>div:nth-child(2){
                flex: 1;
                text-align: center;
                font-size: 18px;
                color: #FFFEFE;
                font-weight: bold;
            }
        }
        .cen{
            color: #656565;
            .list{
                margin: 0 auto;
                padding: 0 50px;
                display: flex;
                justify-content: space-between;
            }
        }
    }
}
.title{
    font-size: 18px;
    font-weight: bold;
    color: #fff;
    height: 44px;
    line-height: 44px;
    margin: 20px 0;
    text-align: center;
    background: #FFAD01;
}
.con{
    width: 1150px;
    margin: 0 auto;
    .text{
        width: 100%;
        min-height: 372px;
        padding: 18px 20px;
        border: 1px solid #E3E3E3;
        textarea:focus{
            border: none !important;
        }
        textarea::-webkit-input-placeholder,
            textarea::-webkit-input-placeholder {
            color: #cdcdcd;
        }
        textarea{
            font-size: 18px;
            width: 100%;
            height: 100px;
            background: none !important;
            border: none !important;
            outline: none;
            resize:none;
            overflow: hidden;
        }
        .xingji{
            margin-top: 42px;
            font-size: 16px;
            display: flex;
            justify-content: left;
            align-items: center;
            &>div:nth-child(1){
                margin-right: 19px;
            }
        }
    }
    .fabu{
        text-align: right;
        div{
            display: inline-block;
            width: 112px;
            height: 42px;
            margin-top: 37px;
            text-align: center;
            line-height: 42px;
            color: #fff;
            font-size: 20px;
            background: #FFAD01;
        }
    }
    .lishi{
        .title1{
            padding-bottom: 31px;
            text-align: center;
            margin-top: 79px;
            color: #FFAD01;
            font-size: 18px;
            font-weight: bold;
        }
        .list{
            min-height: 200px;
            margin-bottom: 50px;
            border-top: 1px solid #E3E3E3;
            border-bottom: 1px solid #E3E3E3;
            &>div:last-child{
                border-bottom: none;
            }
            .item{
                padding: 30px 4px 40px 7px;
                display: flex;
                align-items: top;
                border-bottom: 1px solid #E3E3E3;
                .left{
                    width: 150px;
                    color:#4E4E4E;
                    font-size: 16px;
                }
                .right{
                    flex: 1;

                    &>div:nth-child(1){

                    }
                    &>div:nth-child(2){
                        margin: 20px 0 30px;
                        font-size: 16px;
                    }
                    &>div:nth-child(3){
                        display: flex;
                        align-items: center;
                        div{
                            width: 68px;
                            height: 68px;
                            margin-right: 17px;
                        }
                    }
                    &>div:nth-child(4){
                        color: #8F8F8F;
                        font-size: 16px;
                        margin-top: 28px;
                    }
                }
            }
        }
    }
}
</style>


